.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #22ff22;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 0.8s linear infinite;
}



@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
